$counter-stage-1: #52aafb;
$counter-stage-2: #915cb6;
$counter-stage-3: #f6830f;
$counter-stage-4: #aa3a3a;

$background-1: #f6f5f5;
$background-2: #282c34;

.App {
  text-align: center;
}

.App-header {
  // background-color: #282c34;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .button {
    background-color: #52aafb;
    border: none;
    color: rgba(255, 255, 255, 0);
    padding: 3rem 5rem;
    border-radius: 0.25rem;
    font-size: 3rem;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin-top: 1rem;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    &:hover {
      color: rgba(255, 255, 255, 1);
      box-shadow: 10px 30px 100px rgba(135, 189, 226, 1);
    }
    &:focus {
      outline: none;
      border: none;
    }
  }
  .timer-display {
    font-size: 15rem;
    font-weight: bolder;
    text-shadow: 10px 25px 200px;
    font-family: "Quintessential";
  }

  @keyframes blinker {
    50% {
      opacity: 0;
    }
  }
}
